import { FC, useState } from "react";
import { Box, Button, Flex, Stack, Text, Badge, useColorModeValue } from "@chakra-ui/react";
import { configs } from "shared/content/Content";
import { OtherProjectCard } from "pages/other-projects/other-project-card/OtherProjectCard";
import { ChevronDownIcon, ChevronUpIcon } from "utils/Icons";
import { Tags } from "shared/tags/Tags";
import { ProjectCardFooter } from "shared/project-card-footer/ProjectCardFooter";

const initialCount = 3;
const incrementor = 3;

export const OtherProjects: FC = () => {
  const [count, setCount] = useState<number>(initialCount);
  const textColor = useColorModeValue("gray.600", "gray.300"); 

  const onShowMore = () => {
    setCount(prev => prev + incrementor);
  };

  const onShowLess = () => {
    setCount(initialCount);
  };

  const ongoingProjects = configs.otherProjects.filter(p => p.status === "ongoing");
  const previousProjects = configs.otherProjects.filter(p => p.status === "previous");

  return (
    <Stack spacing={10}>
      {/* Ongoing Projects */}
      <Box>
        {/* <Box fontWeight="bold" fontSize="20px" mb={4}>
          Ongoing
        </Box> */}
        <Stack spacing={6}>
          {ongoingProjects.map((project, index) => (
            <Flex
              key={project.id}
              direction={{ base: "column", md: "row" }}
              gap={4}
              align="flex-start"
            >
              {/* ÏôºÏ™Ω Î∞ïÏä§ */}
              <Box
                flex={{ base: "none", md: "0.85" }}
                w={{ base: "100%", md: "auto" }}
                p={2}
                textAlign="right"
              >
                <Text fontSize="18" fontWeight="bold">
                  Project #{10 - index}
                </Text>

                <Box display="right" alignItems="baseline" mb={1}>
                 
                  <Box
                    color={textColor}
                    fontWeight="bold"
           
                    fontSize="16"
          
                  >
                     {project.recent === "yes" && (
                    <Badge borderRadius="full" colorScheme="teal" mr="1">
                      New 
                    </Badge>
                  )}
                    {project.year}
                  </Box>

                  {/* ‚úÖ ProjectCardFooter Ï∂îÍ∞Ä */}
                  {/* <ProjectCardFooter
                    readMore={(project as any).readMore}
                    github={project.github}
                    demo={project.demo}
                    article={(project as any).article}
                    figshare={project.figshare}
                    paper={(project as any).paper}
                  /> */}
                </Box>
              </Box>

              {/* Ïò§Î•∏Ï™Ω Ïπ¥Îìú */}
              <Box flex={{ base: "none", md: "3.15" }} w="100%">
                <OtherProjectCard {...project} />
              </Box>
            </Flex>
          ))}
        </Stack>
      </Box>

      <hr />

      {/* Previous Projects */}
      <Box>
        {/* <Box fontWeight="bold" fontSize="20px" mb={4}>
          Previous
        </Box> */}
        <Stack spacing={6}>
          {previousProjects.slice(0, count).map((project, index) => (
            <Flex
              key={project.id}
              direction={{ base: "column", md: "row" }}
              gap={4}
              align="flex-start"
            >
              {/* ÏôºÏ™Ω Î∞ïÏä§ */}
              <Box
                flex={{ base: "none", md: "0.85" }}
                w={{ base: "100%", md: "auto" }}
                p={2}
                textAlign="right"
              >
                <Text fontSize="18" fontWeight="bold">
                  Project #{ongoingProjects.length - index + 4}
                </Text>
                <Text
                  color={textColor}
                  fontWeight="bold"
                  fontSize="16"
                >
                  {project.year}
                </Text>

                {/* ‚úÖ ProjectCardFooter Ï∂îÍ∞Ä */}
                {/* <ProjectCardFooter
                  readMore={(project as any).readMore}
                  github={project.github}
                  demo={project.demo}
                  article={(project as any).article}
                  figshare={project.figshare}
                /> */}
              </Box>

              {/* Ïò§Î•∏Ï™Ω Ïπ¥Îìú */}
              <Box flex={{ base: "none", md: "3.15" }} w="100%">
                <OtherProjectCard {...project} />
              </Box>
            </Flex>
          ))}
        </Stack>

        {/* Î≤ÑÌäº */}
        {previousProjects.length > initialCount && (
          <Flex mt={4} mb={4} justify="end">
            {count < previousProjects.length ? (
              <Button leftIcon={<ChevronDownIcon />} onClick={onShowMore}>
                Show More
              </Button>
            ) : (
              <Button leftIcon={<ChevronUpIcon />} onClick={onShowLess}>
                Show Less
              </Button>
            )}
          </Flex>
        )}
      </Box>
    </Stack>
  );
};
