import { FC, useState } from "react";
import {
  Box,
  Image,
  Flex,
  Heading,
  Text,
  Button,
  useColorModeValue,
  Wrap,
  WrapItem,
  Tooltip,
  Stack
} from "@chakra-ui/react";
// import { ExternalLinkIcon } from "@chakra-ui/icons";
import { Tags } from "shared/tags/Tags";
import { ProjectCardFooter } from "shared/project-card-footer/ProjectCardFooter";
import { bgDarker, bgLighter } from "theme";

export type Paper = {
  title: string;
  url: string;
  venue?: string;
  year?: string;
};

interface Props {
  id: string;
  title: string;
  demo?: string;
  github?: string;
  tags: string[];
  description: string;
  readMore?: string;
  image: string;
  jpg: string;
  year: string;
  recent: string;
  article?: string;
  figshare?: string;
  paper?: string;
  /** ÌîÑÎ°úÏ†ùÌä∏ ÎÖºÎ¨∏ Î™©Î°ù (Î≤ÑÌäºÏúºÎ°ú Î†åÎçîÎßÅ) */
  papers?: Paper[];
}

export const OtherProjectCard: FC<Props> = ({
  
  id,
  title,
  demo,
  github,
  figshare,
  paper,
  article,
  tags,
  description,
  readMore,
  image,
  jpg,
  year,
  recent,
  papers = [],
}) => {
  const [expanded, setExpanded] = useState(false);
  const showMoreCut = 3;
  const visiblePapers = expanded ? papers : papers.slice(0, showMoreCut);
  const bg = useColorModeValue(bgLighter, bgDarker);
  const textColor = useColorModeValue("gray.600", "gray.300");

  return (
    <Box
      id={`other-project-card-${id}`}
      borderWidth="1px"
      borderRadius="lg"
      overflow="hidden"
      boxShadow="md"
      transition="all 0.2s ease-in-out"
      bg="bg"
      textColor="textColor"
      p={0}
    >
      <Flex direction={{ base: "column", md: "row" }} alignItems="stretch" gap={4}>
        {/* ÏôºÏ™Ω: Ïù¥ÎØ∏ÏßÄ */}
        <Box flex={{ base: "1", md: "0.35" }} minW="140px">
          <picture>
            <source type="image/webp" srcSet={image} />
            <source type="image/jpeg" srcSet={jpg} />
            <Image
              src={image}
              alt={`${title}-cover-image`}
              objectFit="cover"
              w="100%"
              h="100%"
              borderLeftRadius={{ md: "lg" }}
              borderTopRadius={{ base: "lg", md: "none" }}
            />
          </picture>
        </Box>

        {/* Ïò§Î•∏Ï™Ω: ÎÇ¥Ïö© */}
        <Box flex={{ base: "1", md: "0.65" }} p="6">
          <Heading fontSize="lg" mb={2}>
            {title}
          </Heading>

          <Text mb={1} fontSize="md">
            {description}
          </Text>

          <Tags tags={tags} id={id} size="xs" />

          
          {papers.length > 0 && (
            <Box mb={3}>
              {/* ÏÑ∏Î°ú Î¶¨Ïä§Ìä∏Î°ú, Í∞Å Î≤ÑÌäºÏù¥ Ïπ¥Îìú ÎÑàÎπÑÎ•º Í∞ÄÎìù Ïì∞Í≤å */}
              <Stack spacing="2">
                {visiblePapers.map((p, idx) => (
                  <Button
                    key={`${p.url}-${idx}`}
                    as="a"
                    href={p.url}
                    target="_blank"
                    rel="noopener noreferrer"
                    size="sm"
                    variant="subtle"            // Chakra v2Îäî "ghost"ÎÇò "outline"ÎèÑ OK
                    colorScheme="blue"
                    w="100%"                     // Í∞ÄÎ°úÎ°ú Í∏∏Í≤å
                    justifyContent="flex-start"  // ÌÖçÏä§Ìä∏Î•º ÏôºÏ™Ω Ï†ïÎ†¨
                    height="auto"                // Ïó¨Îü¨ Ï§Ñ ÌóàÏö©
                    py={1}
                    px={1}
                    
                    borderRadius="lg"
                    _hover={{ transform: "translateY(-1px)", boxShadow: "md" }}
                    _active={{ transform: "translateY(0px)" }}
                  >
                    <Flex w="100%" align="center" gap={2}>
                     
                      <Box flex="1" minW={0}>
                        <Text fontWeight="semibold" noOfLines={2}>
                          üìÑ {p.title}
                        </Text>
                        {(p.venue || p.year) && (
                          <Text fontSize="xs" color="gray.500" noOfLines={1}>
                            {[p.venue, p.year].filter(Boolean).join(", ")}
                          </Text>
                        )}
                      </Box>
                    </Flex>
                  </Button>
                ))}
                {papers.length > showMoreCut && (
                  <Button
                    size="xs"
                    variant="ghost"
                    onClick={() => setExpanded(v => !v)}
                    w="100%"
                  >
                    {expanded ? "Ï†ëÍ∏∞" : `+${papers.length - showMoreCut} ÎçîÎ≥¥Í∏∞`}
                  </Button>
                )}
              </Stack>
            </Box>
          )}

          <ProjectCardFooter
            readMore={readMore}
            github={github}
            demo={demo}
            article={article}
            figshare={figshare}
            paper={paper}
          />
        </Box>
      </Flex>
    </Box>
  );
};
